import React from 'react';
import { Link } from 'react-router-dom';

const Header = () => {
    return (
        <div className="ui teal top menu inverted">
            <Link to="/" className="item">
                M*System
            </Link>
            <div className="right menu">
                <Link to="/" className="item">
                    Homes List
                </Link>
                <Link to="/homes/new" className="item">
                    New Home
                </Link>
            </div>
        </div>
    );
};

export default Header;